import React, { useState, useEffect } from "react";
import {
  Box,
  Button,
  FormControl,
  FormControlLabel,
  FormHelperText,
  FormLabel,
  Grid,
  InputLabel,
  MenuItem,
  Radio,
  RadioGroup,
  Select,
  TextField,
  Autocomplete,
  Chip,
} from "@mui/material";
import axios from "axios";
import { Popup } from "../../../Components/Popup";
import LeadServices from "../../../services/LeadService";
import { CustomLoader } from "../../../Components/CustomLoader";
import { CreateAllCompanyDetails } from "./../../Cutomers/CompanyDetails/CreateAllCompanyDetails";
import InventoryServices from "../../../services/InventoryService";
import { CreateAllVendorDetails } from "./CreateAllVendorDetails";

export const CreateVendorDetails = (props) => {
  const { getAllVendorDetails } = props;
  const [openPopup2, setOpenPopup2] = useState(false);
  const [open, setOpen] = useState(false);
  const [typeData, setTypeData] = useState("");
  const [gstFocused, setGstFocused] = useState(false);
  const [panFocused, setPanFocused] = useState(false);

  const [inputValue, setInputValue] = useState({
    name: "",
    address: "",
    pincode: "",
    website: "",
    estd_date: "",
    gst_number: "",
    pan_number: "",
    total_sales_turnover: "",
  });
  const [pinCodeData, setPinCodeData] = useState([]);
  const [idForEdit, setIdForEdit] = useState("");
  const [assigned, setAssigned] = useState([]);
  const [assign, setAssign] = useState([]);
  const [errorMessage, setErrorMessage] = useState([]);
  const handleChange = (event) => {
    setTypeData(event.target.value);
  };

  const handleInputChange = (event) => {
    const { name, value } = event.target;
    setInputValue({ ...inputValue, [name]: value });
  };

  const validatePinCode = async () => {
    try {
      setOpen(true);
      const PINCODE = inputValue.pin_code;
      const response = await axios.get(
        `https://api.postalpincode.in/pincode/${PINCODE}`
      );

      setPinCodeData(response.data[0].PostOffice[0]);
      setOpen(false);
    } catch (error) {
      console.log("Creating Bank error ", error);
      setOpen(false);
    }
  };

  useEffect(() => {
    getLAssignedData();
  }, []);

  const getLAssignedData = async (id) => {
    try {
      setOpen(true);
      const res = await LeadServices.getAllAssignedUser();
      setAssigned(res.data);
      setOpen(false);
    } catch (error) {
      console.log("error", error);
      setOpen(false);
    }
  };
  console.log("typeData", typeData);
  // Function to validate GST number
  const GST_NO = (gstNumber) => {
    if (gstNumber.length > 15) {
      return true;
    }
    return false;
  };

  // Function to validate PAN number
  const validatePanNumber = (panNumber) => {
    const regex = /[A-Z]{5}[0-9]{4}[A-Z]{1}/;
    if (regex.test(panNumber)) {
      return false;
    }
    return true;
  };
  console.log("inputValue gst_number", inputValue.gst_number);
  const gstError =
    typeData === "Domestic" &&
    gstFocused &&
    (inputValue.gst_number === "" || GST_NO(inputValue.gst_number.toString()))
      ? "GST NO should be less than or equal to 15 Digit"
      : "";

  const panError =
    typeData === "Domestic" &&
    panFocused &&
    (inputValue.pan_number === "" || validatePanNumber(inputValue.pan_number))
      ? "Invalid PAN No."
      : "";

  const createCompanyDetails = async (e) => {
    try {
      e.preventDefault();
      setOpen(true);
      if (gstError || panError) {
        setOpen(false);
        return;
      }
      const req = {
        type: typeData,
        name: inputValue.name,
        address: inputValue.address,
        pincode: inputValue.pin_code,
        state: pinCodeData.State,
        city: pinCodeData.District,
        website: inputValue.website_url,
        estd_date: inputValue.estd_date,
        gst_number: inputValue.gst_number,
        pan_number: inputValue.pan_number,
        total_sales_turnover: inputValue.total_sale,
      };
      const response = await InventoryServices.createVendorData(req);
      setIdForEdit(response.data.vendor_id);
      // setOpenPopup(false);
      setOpen(false);
      setOpenPopup2(true);
      // getAllVendorDetails();
    } catch (error) {
      console.log("createing company detail error", error);
      setErrorMessage(
        error.response.data.errors
          ? error.response.data.errors.pan_number
          : error.response.data.errors
      );
      setOpen(false);
    }
  };

  return (
    <div>
      <CustomLoader open={open} />

      <Box
        component="form"
        noValidate
        onSubmit={(e) => createCompanyDetails(e)}
      >
        <Grid container spacing={2}>
          <Grid item xs={12}>
            <>
              <FormControl>
                <FormLabel id="demo-row-radio-buttons-group-label">
                  Type
                </FormLabel>
                <RadioGroup
                  row
                  aria-labelledby="demo-row-radio-buttons-group-label"
                  name="row-radio-buttons-group"
                  value={typeData}
                  onChange={handleChange}
                >
                  <FormControlLabel
                    value="international"
                    control={<Radio />}
                    label="international"
                  />
                  <FormControlLabel
                    value="Domestic"
                    control={<Radio />}
                    label="Domestic"
                  />
                </RadioGroup>
              </FormControl>
            </>
          </Grid>

          <Grid item xs={12} sm={4}>
            <TextField
              fullWidth
              name="name"
              size="small"
              label="Vendor Name"
              variant="outlined"
              value={inputValue.name}
              onChange={handleInputChange}
            />
          </Grid>

          <Grid item xs={12} sm={4}>
            <TextField
              sx={{ minWidth: "200px" }}
              name="pin_code"
              size="small"
              type={"number"}
              label="Pin Code"
              variant="outlined"
              value={inputValue.pin_code}
              onChange={handleInputChange}
            />
            <Button
              onClick={() => validatePinCode()}
              variant="contained"
              sx={{ marginLeft: "1rem" }}
            >
              Validate
            </Button>
          </Grid>

          <Grid item xs={12} sm={4}>
            <TextField
              fullWidth
              size="small"
              name="state"
              label="State"
              variant="outlined"
              value={pinCodeData.State ? pinCodeData.State : ""}
            />
          </Grid>
          <Grid item xs={12} sm={4}>
            <TextField
              fullWidth
              name="city"
              size="small"
              label="City"
              variant="outlined"
              value={pinCodeData.District ? pinCodeData.District : ""}
            />
          </Grid>

          <Grid item xs={12} sm={4}>
            <TextField
              fullWidth
              size="small"
              name="website_url"
              label="website Url"
              variant="outlined"
              value={inputValue.website_url}
              onChange={handleInputChange}
            />
          </Grid>
          <Grid item xs={12} sm={4}>
            <TextField
              fullWidth
              type="date"
              name="estd_date"
              size="small"
              label="Estd.Date"
              variant="outlined"
              value={inputValue.estd_date}
              onChange={handleInputChange}
              InputLabelProps={{
                shrink: true,
              }}
            />
          </Grid>
          <Grid item xs={12} sm={4}>
            <TextField
              fullWidth
              size="small"
              name="gst_number"
              label="GST No."
              variant="outlined"
              value={inputValue.gst_number}
              onChange={handleInputChange}
              error={gstError !== ""}
              helperText={gstError}
              onFocus={() => setGstFocused(true)}
              onBlur={() => setGstFocused(false)}
            />
          </Grid>
          <Grid item xs={12} sm={4}>
            <TextField
              fullWidth
              required
              size="small"
              name="pan_number"
              label="Pan No."
              variant="outlined"
              value={inputValue.pan_number}
              onChange={handleInputChange}
              error={panError !== ""}
              helperText={panError}
              onFocus={() => setPanFocused(true)}
              onBlur={() => setPanFocused(false)}
            />
          </Grid>
          <Grid item xs={12} sm={4}>
            <TextField
              fullWidth
              name="total_sale"
              size="small"
              type={"number"}
              label="Total Sale"
              variant="outlined"
              value={inputValue.total_sale}
              onChange={handleInputChange}
            />
          </Grid>

          <Grid item xs={12}>
            <TextField
              multiline
              fullWidth
              name="address"
              size="small"
              label="Address"
              variant="outlined"
              value={inputValue.address}
              onChange={handleInputChange}
            />
          </Grid>
        </Grid>
        <Button
          type="submit"
          fullWidth
          variant="contained"
          sx={{ mt: 3, mb: 2 }}
        >
          Submit
        </Button>
      </Box>
      <Popup
        maxWidth={"lg"}
        title={"Create Company Details"}
        openPopup={openPopup2}
        setOpenPopup={setOpenPopup2}
      >
        <CreateAllVendorDetails
          setOpenPopup={setOpenPopup2}
          getAllVendorDetails={getAllVendorDetails}
          recordForEdit={idForEdit}
        />
      </Popup>
    </div>
  );
};
